/* Updated styles for a more modern, interactive tutorial interface */

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary: #6366f1;
  --primary-dark: #4f46e5;
  --secondary: #ec4899;
  --accent: #8b5cf6;
  --success: #10b981;
  --warning: #f59e0b;
  --danger: #ef4444;
  --dark-bg: #111827;
  --dark-card: #1f2937;
  --dark-accent: #374151;
  --light-text: #f9fafb;
  --light-text-muted: #9ca3af;
  --js-yellow: #f7df1e;
  --ts-blue: #3178c6;
  --html-orange: #e34c26;
  --css-blue: #264de4;
}

body {
  @apply bg-gray-900 text-gray-100;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

/* Modern Card Styles */
.card {
  @apply bg-gray-800 rounded-xl shadow-lg border border-gray-700 overflow-hidden transition-all duration-300;
}

.card:hover {
  @apply shadow-xl border-gray-600 transform translate-y-[-4px];
}

/* Button Styles */
.btn-primary {
  @apply px-4 py-2 bg-indigo-600 hover:bg-indigo-700 text-white rounded-lg font-medium transition-colors duration-200 flex items-center justify-center;
}

.btn-secondary {
  @apply px-4 py-2 bg-gray-700 hover:bg-gray-600 text-white rounded-lg font-medium transition-colors duration-200 flex items-center justify-center;
}

.btn-accent {
  @apply px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg font-medium transition-colors duration-200;
}

/* Navigation Styles */
.nav-link {
  @apply px-3 py-2 rounded-lg transition-colors duration-200 hover:bg-gray-700 hover:text-white;
}

.nav-link.active {
  @apply bg-indigo-600 text-white;
}

/* Progress Indicators */
.progress-bar {
  @apply h-2 rounded-full bg-gray-700 overflow-hidden;
}

.progress-bar-fill {
  @apply h-full rounded-full transition-all duration-500 ease-out;
}

.progress-bar-fill.js {
  @apply bg-yellow-400;
}

.progress-bar-fill.ts {
  @apply bg-blue-500;
}

.progress-bar-fill.html {
  @apply bg-orange-500;
}

.progress-bar-fill.css {
  @apply bg-blue-600;
}

/* Topic List Styles */
.topic-item {
  @apply flex items-center p-4 rounded-lg transition-colors duration-200 border-l-4 border-transparent;
}

.topic-item:hover {
  @apply bg-gray-800 border-l-4 border-indigo-500;
}

.topic-item.completed {
  @apply border-l-4 border-green-500;
}

.topic-item.active {
  @apply bg-gray-800 border-l-4 border-indigo-500;
}

/* Code Block Styling */
.code-block {
  @apply my-4 rounded-lg overflow-hidden text-sm;
  background-color: #1a1a1a !important;
}

.code-block .line-number {
  @apply inline-block w-8 text-right pr-2 text-gray-500 select-none border-r border-gray-700 mr-3;
}

.code-block .line-content {
  @apply inline-block;
}

/* Interactive Elements */
.interactive-demo {
  @apply border border-gray-700 rounded-lg p-4 my-6 bg-gray-800;
}

.interactive-demo-header {
  @apply flex justify-between items-center mb-4 pb-2 border-b border-gray-700;
}

.interactive-demo-content {
  @apply relative;
}

.interactive-demo-controls {
  @apply flex gap-2 mt-4 pt-4 border-t border-gray-700;
}

/* Tabs Component */
.tabs {
  @apply flex border-b border-gray-700 mb-4;
}

.tab {
  @apply px-4 py-2 font-medium transition-colors duration-200 border-b-2 border-transparent;
}

.tab:hover {
  @apply text-white border-gray-500;
}

.tab.active {
  @apply text-white border-indigo-500;
}

/* Markdown Content Styling */
.markdown-content {
  @apply text-gray-200 leading-relaxed;
}

.markdown-content h1 {
  @apply text-3xl font-bold mb-6 text-white;
}

.markdown-content h2 {
  @apply text-2xl font-bold mt-8 mb-4 text-white;
}

.markdown-content h3 {
  @apply text-xl font-bold mt-6 mb-3 text-white;
}

.markdown-content p {
  @apply mb-4;
}

.markdown-content ul, .markdown-content ol {
  @apply mb-4 ml-6;
}

.markdown-content ul {
  @apply list-disc;
}

.markdown-content ol {
  @apply list-decimal;
}

.markdown-content li {
  @apply mb-2;
}

.markdown-content a {
  @apply text-indigo-400 hover:text-indigo-300 underline;
}

.markdown-content blockquote {
  @apply border-l-4 border-gray-600 pl-4 italic my-4 text-gray-400;
}

.markdown-content table {
  @apply w-full border-collapse my-4;
}

.markdown-content th {
  @apply bg-gray-800 p-2 text-left border border-gray-700;
}

.markdown-content td {
  @apply p-2 border border-gray-700;
}

/* Animation Classes */
.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.slide-in {
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

/* Tooltip Styles */
.tooltip {
  @apply relative inline-block;
}

.tooltip .tooltip-text {
  @apply invisible absolute z-10 p-2 bg-gray-900 text-white text-sm rounded-md shadow-lg opacity-0 transition-opacity duration-200 w-max max-w-xs;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip:hover .tooltip-text {
  @apply visible opacity-100;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-800;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-600 rounded-full;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-gray-500;
}

/* Interactive Code Editor */
.code-editor {
  @apply font-mono text-sm rounded-lg overflow-hidden border border-gray-700;
}

.code-editor-header {
  @apply flex items-center justify-between p-2 bg-gray-800 border-b border-gray-700;
}

.code-editor-actions {
  @apply flex gap-2;
}

.code-editor-action {
  @apply w-6 h-6 rounded-full flex items-center justify-center transition-colors duration-200;
}

.code-editor-content {
  @apply p-4 bg-gray-900 max-h-96 overflow-auto;
}

.code-editor-footer {
  @apply flex justify-between items-center p-2 bg-gray-800 border-t border-gray-700;
}

/* Callout Boxes */
.callout {
  @apply p-4 rounded-lg my-4 border-l-4;
}

.callout-title {
  @apply font-bold text-lg mb-2 flex items-center;
}

.callout-content {
  @apply text-sm;
}

.callout.info {
  @apply bg-blue-900/30 border-blue-500;
}

.callout.warning {
  @apply bg-yellow-900/30 border-yellow-500;
}

.callout.tip {
  @apply bg-green-900/30 border-green-500;
}

.callout.important {
  @apply bg-purple-900/30 border-purple-500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .markdown-content h1 {
    @apply text-2xl;
  }
  
  .markdown-content h2 {
    @apply text-xl;
  }
  
  .markdown-content h3 {
    @apply text-lg;
  }
}
